/**
 * @file 通用的函数类
 *
 * @author fengchaoning@baidu.com
 */
export default {
    alert (msg, fn) {
        const tpl = `
            <div id="__permission_alert" class="__permission-alert">
                <div class="__alert-header">
                    <h3>系统提示</h3>
                </div>
                <div class="__alert-body">
                    <span>${msg}</span>
                </div>
                <div class="__alert-btns">
                    <a id="__permission_btn" class="__permission_btn" href="javascript:;">确定</a>
                </div>
            </div>
            <div class="mask" id="mask"></div>
            `;

        $(document.body).append(tpl);
        this.bindEvent(fn);
    },
    loading () {
        const tpl = `
            <div __floater-loading>
                <span>正在努力的加载中...</span>
            </div>
            <div class="mask" id="mask"></div>
        `;
        $(document.body).append(tpl);
    },
    bindEvent (fn) {
        $('#__permission_btn').on('click', () => {
            fn && fn();
            $('#__permission_alert, #mask').remove();
        });
    }
};



