
import vm from 'VueModel';
import style from './index.styl';
import tpl from './index.tpl.html';
import utils from '../../utils';

module.exports = vm.getComponent({
    ready () {
        this.init();
    },
    template: tpl,
    data () {
        return {
            tradeId: '-1',
            tradeGraphDowns: [],
            tradeGraphUps: [],
            downTrades: [],
            upTrades: []
        };
    },
    components: {},
    events: {},
    methods: {
        init () {
        },
        saveForm () {
            const formData = {
                tradeId: this.tradeId,
                custId: '1111',
                tradeGraphDowns: this.downTrades,
                tradeGraphUps: this.upTrades
            };
            $.request('/Nakri/trade/saveCustTrade', {
                type: 'POST',
                data: formData
            }).then(res => {
                if (res.status === 0) {
                    utils.alert('保存成功', () => {
                        window.router.go('/industry');
                    });
                }
            }, res => {
                utils.alert('系统网络故障，请稍候重试');
            });
        },
        getIndustry () {
            const me = this;
            if (this.tradeId === '-1') {
                return false;
            }
            const param = {
                tradeId: this.tradeId
            };
            $.request('/Nakri/trade/showTradeUpAndDown', {
                type: 'GET',
                data: param
            }).then(res => {
                me.tradeGraphUps = res.data.tradeGraphUps || [];
                me.tradeGraphDowns = res.data.tradeGraphDowns || [];
            }, res => {
                return [];
            });
        }
    }
});



